<template>
  <div id="app">
    <!--<div id="nav">-->
    <!--<router-link to="/">Home</router-link>-->
    <!--|-->
    <!--<router-link to="/about">About</router-link>-->
    <!--</div>-->
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods: {
    // 这下面的 1920 跟 19.2 这两个数字，都是开发完后再补的
    // 效果虽然呈现了，但不是很好，注意了
    refreshRem () {
      const docEl = window.document.documentElement
      let width = docEl.getBoundingClientRect().width
      width = width > 1920 ? 1920 : width
      const rem = width / 19.2
      docEl.style.fontSize = rem + 'px'
    },
    refreshRem2 () {
      const docEl = window.document.documentElement
      let width = docEl.getBoundingClientRect().width
      width = width > 512 ? 512 : width
      const rem = width / 10
      docEl.style.fontSize = rem + 'px'
    }

  },
  mounted () {
    const isMobile = (this.$route.name !== 'home' || window.location.href.indexOf('pc') === -1)
    isMobile ? this.refreshRem2() : this.refreshRem()
    const win = window
    let tid = null
    win.addEventListener('resize', () => {
      clearTimeout(tid)
      isMobile ? tid = setTimeout(this.refreshRem2, 300) : tid = setTimeout(this.refreshRem, 300)
    }, false)
  }
}
</script>

