<script>

const EVENT_ON_START = 'onStart'
const EVENT_ON_END = 'onEnd'
const EVENT_ON_CLOSE = 'onClose'

export default {
  name: 'threeDBox',
  data () {
    return {
      delay: false,     // 这只是一个动画的延迟
      working: false,   // 转动中

      move: false,      // 这是一串连贯动作
      down: false,
      up: false,
      rotate: false,
      move1: false,

      havePrize: false,   // 后台是否返回抽奖的结果

      prizesNum: '',      // 一次性抽的奖的个数
      prizesArray: [      // 抽的奖的个数数组
        { text: '单个', value: '1' },
        // { text: '10个', value: '10' },
        // { text: '20个', value: '20' },
        // { text: '30个', value: '30' },
        // { text: '50个', value: '50' },
        { text: '全部', value: 'all' }
      ],
      testxx: ''
    }
  },
  props: {
    prize: {
      type: Object,
      'default': {}
    },
    luckyMan: {
      type: Object,
      'default': function () {
        return {}
      }
    }
  },
  methods: {
    // 设计一个延时，避免用户误点两次，直接抽奖
    _setDelay () {
      this.delay = true
      window.setTimeout(() => {
        this.delay = false
      }, 1500)
    },

    // http 是否获取到结果，这个结果是最终抽奖的依据
    httpHasGetResult () {
      this.havePrize = true
    },

    // 开始转动/停止转动
    // 当非转动时（working=false)，则开始转动，并发出转动通知
    // 当转动中时（working=true)，则停止转动，进入手动画
    start: function () {
      if (this.move || this.delay) { // 延迟时，或手在动画中，不支持响应
        return false
      }

      if (this.working === false) {
        this.working = true
        this._setDelay()
        this.$emit(EVENT_ON_START, this.prizesNum)
      } else {            // 停止
        this.working = false
        this.showHand()
      }
    },

    // 执行手动画
    // 这里用到一个 opacity 来执行渐变
    showHand () {
      this.move = true
      this.$refs.theHand.style.opacity = 1

      window.setTimeout(() => {
        this.down = true
      }, 500)

      // 此时做一个定时，如果 http 没有返回结果，这只手只能 down 不能up
      // 需要得到 http 结果，这只手才能 up
      let timer = window.setInterval(() => {
        if (this.hasPrize === false) {
          return
        }
        window.clearInterval(timer)
        window.setTimeout(() => {
          this.up = true
          this.$parent.music.play('yeah')       // 当手 up 时，可播放欢呼音乐
        }, 500 + 1200)

        window.setTimeout(() => {
          this.rotate = true
          this.move1 = true
        }, 500 + 1200 + 1200)

        window.setTimeout(() => {
          this.$refs.theHand.style.opacity = 0
        }, 500 + 1200 + 1200 + 1000)

        window.setTimeout(() => {
          this.resetAllAndEmitEnd()
        }, 500 + 1200 + 1000 + 1000 + 1000)
      }, 1000)
    },

    // 重置所有动画，并通知 end 结果
    resetAllAndEmitEnd () {
      this.delay = false
      this.working = false

      this.move = false
      this.down = false
      this.up = false
      this.rotate = false
      this.move1 = false

      this.havePrize = false

      this.$emit(EVENT_ON_END)
    },

    // 当出错时，强行停止
    forceEnd () {
      this.working = false
      this.move = false
      this.down = false
    },

    /**
     * 分发 close 事件，注意，旋转中、手动画中，不可分发 close 事件
     */
    emitClose () {
      if (this.delay || this.working || this.move) {
        return
      }
      this.$emit(EVENT_ON_CLOSE)
    }
  },
  components: {},
  watch: {
    prize (newV, oldV) {
      if (newV.remainNum > 10) {
        this.prizesNum = 'all'
      } else {
        this.prizesNum = '1'
      }
    }
  },
  mounted () {
    console.log(window.td = this)
  }
}
</script>

