import Vue from 'vue'

export default {
  url: 'https://promotion.phone580.com/luck/external/',        // 生产 https
  // url: 'http://47.75.125.226:8086/luck/external/',     // 外网
  // url: 'http://10.20.100.103:19106/luck/external/',    // 内网
  /**
   * http promise 请求
   *
   * @param data 传递的参数，不传请设为空值
   * @param reqMethod 额外的 url 方法，请参数 api 文档
   * @param httpType post/get
   * @returns {Promise<any>}
   * @private
   */
  _http (data = {}, reqMethod, httpType = 'post') {
    let paramStr = ''
    if (Object.keys(data).length > 0) {
      Object.keys(data).forEach((item) => {
        paramStr += `${item}=${data[item]}&`
      })
    }
    paramStr = paramStr.slice(0, paramStr.length - 1)
    return new Promise((resolve, reject) => {
      Vue.http.post(this.url + reqMethod, paramStr, { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } }).then(res => {
        if (res.data && res.data.success && res.data.success === true && res.data.vo) {
          resolve(res.data.vo)
        } else {
          reject(res.data)
          console.error(res.data)
        }
      }, err => {
        reject(err)
        console.error(err)
      })
    })
  },
  // 获取所有嘉宾姓名
  getAllPersons () {
    return this._http({}, 'persons')
  },

  // 获取所有奖项
  getAllPrizes () {
    return this._http({}, 'prizes')
  },

  /**
   * 抽奖
   * @param item 奖项item
   * @param count 奖项数量
   * @returns {Q.Promise<any> | {"Content-Type"}}
   */
  prizeDraw (item, count = 'all') {
    count = count === '1' ? 1 : item.num
    // return Vue.http.post(this.url + '',
    //   `perNum=${count}&prizeId=${item.id}`,
    //   {
    //     headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
    //   }
    // )
    return this._http({ perNum: count, prizeId: item.id }, 'prize_draw')
  },

  /**
   * 额外抽奖，抽出那些没人领的奖项
   * @returns {*|Promise<any>}
   */
  rePrizeDraw (item) {
    return this._http({
      prizeId: item.id
    }, 're_prize_draw')
  },

  // 获取节目列表、中奖信息、嘉宾幸运号码
  checkAll (name) {
    return this._http({
      name
    }, 'check_name')
  }
}
