/* eslint-disable */
export default {
  /**
   * 初始化友盟统计 - 添加统计脚本到head
   * @param {String | Int} cnzzId 友盟统计id
   */
  umAnalysisInit (cnzzId) {
    if (window.umAnalysisInited === true) {
      return
    }

    if (!cnzzId) {
      console.error('获取cnzzID失败，无法添加统计代码！')
      return
    }
    let head = document.getElementsByTagName('head')[0]
    if (head === null) {
      console.error('获取head节点失败，无法添加统计代码！')
      return
    }
    
    //声明_czc对象:
    window._czc = window._czc || []
    //绑定siteid
    window._czc.push(['_setAccount', cnzzId])

    let analysisNode = document.createElement('script')
    analysisNode.type = 'text/javascript'
    analysisNode.src = `https://s13.cnzz.com/z_stat.php?id=${cnzzId}&web_id=${cnzzId}`
    head.appendChild(analysisNode)
    window.umAnalysisInited = true
  },
  /**
   * 添加友盟统计事件
   * @param {String} category 必填 表示事件发生在谁身上，如“视频”、“小说”、“轮显层”等等。	
   * @param {String} action 必填 表示访客跟元素交互的行为动作，如"播放"、"收藏"、"翻层"等等。	 
   * @param {String} label 选填 用于更详细的描述事件，如具体是哪个视频，哪部小说。	 
   * @param {Int} value 选填 用于填写打分型事件的分值，加载时间型事件的时长，订单型事件的价格。	请填写整数数值，如果填写为其他形式，系统将按0
   * @param {String} nodeid 选填 填写事件元素的div元素id。	请填写class id，暂不支持name。
   */
  umEventPush (category, action, label, value, nodeid) {
    if (window.umAnalysisInited != true || _czc == null || _czc.push == null) {
      //console.error('统计SDK未初始化！')
      return
    }
    _czc.push(["_trackEvent", category, action, label, value, nodeid])
  }
}