<template>
  <div class="Music" style="display: none;">
    <audio id="showTruePrize" preload>
      <source src="../assets/music/show-true-prize.mp3" type="audio/mpeg">
    </audio>
    <audio id="rotateBackMusic" preload loop>
      <source src="../assets/music/rotate-back-2.mp3" type="audio/mpeg">
    </audio>
    <audio id="luckManShow" preload>
      <source src="../assets/music/luck-man-show.mp3" type="audio/mpeg">
    </audio>
  </div>
</template>

<script>

export default {
  data () {
    return {
      audioLuckManShow: null,
      audioShowTruePrize: null,
      audioRotateBackMusic: null
    }
  },
  methods: {
    /**
     *
     * @param kind rotate/boom/yeah
     */
    play (kind = '') {
      this.pause()
      if (kind === 'rotate') {
        this.audioRotateBackMusic.play()
      } else if (kind === 'boom') {
        this.audioShowTruePrize.play()
      } else if (kind === 'yeah') {
        this.audioLuckManShow.play()
      }
    },
    pause () {
      this.audioLuckManShow.pause()
      this.audioShowTruePrize.pause()
      this.audioRotateBackMusic.pause()
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.audioLuckManShow = document.getElementById('luckManShow')
      this.audioShowTruePrize = document.getElementById('showTruePrize')
      this.audioRotateBackMusic = document.getElementById('rotateBackMusic')
    })
  }
}
</script>

