<template>
  <div class="new-year">
    <div class="ny-logo-1">
      <img src="../assets/images/logo-1.png" alt="">
    </div>
    <div class="ny-logo-2">
      <img src="../assets/images/logo-2.png" alt="">
      <div class="ny-logo-2-1">
        <img src="../assets/images/logo-2-1.png" alt="">
      </div>
      <div class="ny-logo-2-2">
        <img src="../assets/images/logo-2-2.png" alt="">
      </div>
    </div>
    <div class="ny-logo-3">
      <span class="ny-logo-3-1"></span>
      <span class="ny-logo-3-2"></span>
      <img src="../assets/images/logo-3.png" alt="">
    </div>
    <div class="ny-logo-4">
      <img src="../assets/images/logo-4.png" alt="">
    </div>

    <div class="ny-bar">
      <div class="ny-bar-container">
        <div class="ny-bar-item" :class="{'ny-bar-item-active': !item.mask}" v-for="(item, index) in prizeArray"
             :key="index"
             @click="prizeClick(item, index, $event)">
          <div class="ny-bar-prize-fire"></div>
          <div class="ny-bar-prize-txt" :class="{'ny-bar-prize-txt-active': !item.mask}">
            <span v-for="(subItem, subIndex) in item.item" :key="subIndex"> {{subItem}} </span>
          </div>
          <div class="ny-bar-prize-num" v-show="!item.mask">({{item.num-item.remainNum + '-' + item.num}})</div>
          <div class="ny-bar-prize-finished" v-show="item.finished"></div>
        </div>
      </div>
      <div class="ny-bar-extra-fu" @click="extraPrize" v-show="showExtraPrize">
        <div class="ny-bar-extra-fu-fire">
          <span class="fu-fire-1"></span>
          <span class="fu-fire-2"></span>
          <span class="fu-fire-3"></span>
        </div>
      </div>
    </div>

    <transition name="tdb">
      <div class="ny-tdb" v-show="threeDBox_show">
        <ThreeDBox ref="tdb" :prize="currentPrize" :lucky-man="prizePersons[0]" @onStart="tdbStart"
                   @onEnd="tdbEnd()" @onClose="tdbClose()"></ThreeDBox>
      </div>
    </transition>

    <transition name="nyl">
      <div class="ny-list" v-show="prizePersonList_show">
        <PrizePersonList :list="prizePersons" :prize="currentPrize" @onClose="onPrizePersonListClose"></PrizePersonList>
      </div>
    </transition>

    <TipBox ref="tipBox"></TipBox>

    <ConfirmBox ref="confirmBox"></ConfirmBox>

    <Music ref="music"></Music>
  </div>
</template>

<script>
// import HandPrizeBox from '../components/HandPrizeBox'
import ThreeDBox from '../components/ThreeDBox'
import PrizePersonList from '../components/PrizePersonList'
import TipBox from '../components/TipBox'
import ConfirmBox from '../components/ConfirmBox'
import Music from '../components/Music'

import NyHttp from '../Api/index'
import FzsStorage from '../Api/FzsStorage'

export default {
  name: 'home',
  components: {
    TipBox,
    ThreeDBox,
    PrizePersonList,
    ConfirmBox,
    Music
    // HandPrizeBox
  },
  data () {
    return {
      prizeArray: [],
      currentPrize: { item: [] },

      prizePersons: [],     // 中奖人群

      threeDBox_show: false,
      prizePersonList_show: false,
      showExtraPrize: false,

      music: null,
      xx: ''
    }
  },
  methods: {
    _showTip (title, content) {
      this.$refs.tipBox.show(title, content)
    },
    _hideTip () {
      this.$refs.tipBox.hide()
    },
    _prizeDrawError () {
      this.$refs.tdb.forceEnd()
      this.threeDBox_show = false
      this.currentPrize = { item: [] }
    },
    _getClsEle (ele, cls) {
      if (ele) {
        if (ele.className && ele.className.indexOf(cls) >= 0) {
          return ele
        } else if (ele.className && ele.className.indexOf('new-year') >= 0) {
          return false
        } else {
          return this._getClsEle(ele.parentNode, cls)
        }
      } else {
        return false
      }
    },
    // 获取所有奖项
    getAllPrizes () {
      NyHttp.getAllPrizes().then(data => {
        this.prizeArray = data.prizes.map((item, index) => {
          if (item.item.indexOf('0') > -1) {
            item.item = item.item.split(/\b/)
            let tempAry = item.item
            let tempAry2 = []

            for (let i = 0; i < tempAry.length; i++) {
              if (tempAry[i].indexOf('0') === -1) {
                tempAry2 = tempAry2.concat(tempAry[i].split(''))
              } else {
                tempAry2.push(tempAry[i])
              }
            }
            item.item = tempAry2
          } else {
            item.item = item.item.split('')
          }
          item.remainNum = parseInt(item.remainNum)
          item.num = parseInt(item.num)
          item.finished = (item.remainNum === 0)
          item.mask = (item.num === item.remainNum)
          return item
        })
      }, err => {
        console.error(err)
      })
    },
    // 抽取额外奖项：领导即兴/未领取的
    extraPrize () {
      this.currentPrize = {
        'item': ['幸', '运', '福', '袋'],
        'remainNum': 1000,
        'num': 30,
        'id': '999',
        'finished': false,
        'mask': false
      }
      this.tdbShow(this.currentPrize)
    },
    /**
     *  奖项被点击
     *  0. 检测该奖是否已经已抽，如抽，则返回
     *  1. 检测该奖的上一个奖是否已经抽完，已未抽完，提示
     * @param item
     * @param index
     * @param evt
     */
    prizeClick (item, index, evt) {
      if (item.finished) {
        return
      }
      if (index !== 0) {
        if (this.prizeArray[index - 1].finished === false) {
          this.$refs.tipBox.show('提示', '上一个奖项还没有抽完<br>请抽完上一个奖项哦!')
          return
        }
      }
      const ele = this._getClsEle(evt.target, 'ny-bar-item')
      if (item.mask) {
        item.mask = false
        this.$set(this.prizeArray, index, item)

        const fireEle = ele.children[0]
        fireEle.className += ' ny-bar-prize-fire-ani'

        const txtEle = ele.children[1]
        txtEle.className += ' ny-bar-prize-txt-active'

        window.setTimeout(() => {
          fireEle.className = 'ny-bar-prize-fire'
        }, 800)

        window.setTimeout(() => {
          if (item.finished === false) this.tdbShow(item)
        }, 2000)

        this.music.play('boom')
      } else if (!item.finished) {
        this.tdbShow(item)
      }
    },
    // ===================three D box 开始转动，停止手动画、关闭、显示
    tdbShow (item) {
      this.currentPrize = item
      this.threeDBox_show = true

      this.music.play('rotate')
    },
    tdbStart (param) {
      let httpMethod = null
      if (this.currentPrize.id === '999') {
        httpMethod = NyHttp.rePrizeDraw
      } else {
        httpMethod = NyHttp.prizeDraw
      }

      httpMethod.call(NyHttp, this.currentPrize, param).then(data => {
        if (data.length > 0) {
          this.prizePersons = data
          this.$refs.tdb.httpHasGetResult()
          if (this.currentPrize.id !== '999') {
            this.currentPrize.remainNum = param === 'all' ? 0 : this.currentPrize.remainNum - 1
          }
        } else {
          this._showTip('好像出错了', '该奖项已经被抽取~~')
          this._prizeDrawError()
        }
      }, err => {
        this._showTip('好像出错了', err.msg || '网络错误')
        this._prizeDrawError()
      })
    },
    tdbEnd () {
      if (this.currentPrize.remainNum <= 0) {
        this.threeDBox_show = false
        this.music.pause()
      } else {
        window.setTimeout(() => {
          this.music.play('rotate')
        }, 3000)
      }
      this.prizePersonList_show = true
      this.getAllPrizes()
    },
    tdbClose () {
      this.music.pause()
      this.currentPrize = { item: [] }
      this.threeDBox_show = false
    },
    // ================== end
    onPrizePersonListClose () {
      this.prizePersonList_show = false
    }
  },
  mounted () {
    if (window.location.href.indexOf('promotion.phone580.com') >= 0) {
      console.log(window.ffffffStorage = FzsStorage)
      if (FzsStorage.get('key') !== 'fzs2019') {
        return false
      }
    }
    console.log(window.mm = this)

    // 当没有剩余的名额，则此奖已抽完
    // 当剩余名额 === 设置名额，则此奖 mask
    this.getAllPrizes()
    this.showExtraPrize = true

    this.$nextTick(() => {
      this.music = this.$refs.music
    })
  }
}
</script>

<style lang="scss">
  @import "../assets/common";

  @keyframes logo2_2_ani {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes logo2_1_ani {
    0% {
      transform: translateY(-3px);
    }
    50% {
      transform: translateY(3px);
    }
    100% {
      transform: translateY(-3px);
    }
  }

  @keyframes logo3_ani {
    0% {
      transform: translateY(-20px);
    }
    50% {
      transform: translateY(0px);
    }
    100% {
      transform: translateY(-20px);
    }
  }

  .new-year {
    $font-color: #c0201f;
    width: 100%;
    height: 100%;
    background-image: url(../assets/images/bg-1.jpg);
    background-size: 100% 100%;
    position: relative;
    overflow: hidden;
    .ny-logo-1 {
      position: absolute;
      top: .85rem;
      left: .9rem;
      width: 2.5rem;
      img {
        width: 100%;
      }
    }
    .ny-logo-2 {
      position: absolute;
      top: 1.4rem;
      width: 5.14rem;
      height: .94rem;
      margin-left: -6*.5rem;
      left: 50%;
      img {
        width: 100%;
      }
      .ny-logo-2-1 {
        position: absolute;
        top: 0;
        left: 0;
        width: .81rem;
        height: .36rem;
        animation: logo2_1_ani 2.6s linear 1s infinite;
      }
      .ny-logo-2-2 {
        position: absolute;
        top: .07rem;
        left: 1.2rem;
        width: .88rem;
        height: .88rem;
        animation: logo2_2_ani 6s linear 1s infinite;
      }
    }
    .ny-logo-3 {
      position: absolute;
      top: 0;
      right: .6rem;
      width: 2.85rem;
      height: 6.46rem;
      img {
        width: 100%;
        height: 100%;
      }
      @keyframes ny-logo-3-ani {
        0% {
          transform: scale(.5);
        }
        50% {
          transform: scale(2.5);
        }
        100% {
          transform: scale(.5);
        }
      }
      animation: logo3_ani 2.6s linear 1s infinite;
      span {
        position: absolute;
        left: 1.5rem;
        width: 0px;
        height: 0px;
        background: beige;
        border-radius: 50%;
        box-shadow: 0px 0px 40px 16px #fdac27;
      }
      .ny-logo-3-1 {
        top: 1.8rem;
        animation: ny-logo-3-ani linear 5s infinite;
      }
      .ny-logo-3-2 {
        top: 2.8rem;
        animation: ny-logo-3-ani linear 5s infinite 1s;
      }
    }
    .ny-logo-4 {
      position: absolute;
      width: 100%;
      top: 2.9rem;
      img {
        width: 10.80rem;
      }
    }

    .ny-bar {
      position: absolute;
      bottom: 50px;
      left: 0;
      right: 0;
      .ny-bar-container {
        /*background-image: url(../assets/images/nav-bar.png);*/
        /*background-size: 100% 100%;*/
        /*background-repeat: no-repeat;*/
        //width: 1368*1px;
        //height: 164*0.6px;
        display: flex;
        justify-content: center;
        /*margin: 0 auto;*/
      }
      .ny-bar-item {
        background-size: 100% 100%;
        background-image: url('../assets/images/prize-item-1.png');
        width: 1.69*.8rem;
        height: 3.58*.8rem;
        margin-right: .4rem;
        position: relative;
        &:last-child {
          margin-right: 0rem;
        }
        .ny-bar-prize-fire {
          @extend .ny-back-reset;
          background-image: url(../assets/images/prize-item-fire.png);
          width: 2.38rem;
          height: 2.44rem;
          margin-left: -.5rem;
          transform: scale(0);
          transform-origin: 1.2rem 1.7rem;
          display: none;
        }
        .ny-bar-prize-fire-ani {
          display: block;
          animation: ani-nyBarPrizeFire .8s linear 0s;
          animation-fill-mode: forwards;
        }
        @keyframes ani-nyBarPrizeFire {
          0% {
            transform: scale(0);
            transform-origin: 1.2rem 1.7rem;
            opacity: .1;
          }
          50% {
            transform: scale(1);
            transform-origin: 1.2rem 1.35rem;
            opacity: 1;
          }
          100% {
            opacity: .1;
            transform: scale(2);
            transform-origin: 1.2rem 1rem;
          }
        }
        &.ny-bar-item-active {
          transition: all 1.4s .5s;
          background-image: url(../assets/images/prize-item-active.png)
        }
        .ny-bar-prize-txt {
          font-family: '楷体', serif;
          font-size: 0.34rem;
          font-weight: bold;
          color: #fbe709;
          white-space: nowrap;

          flex-direction: column;
          justify-content: center;

          display: flex;
          height: 1.50rem;
          margin: .96rem 0 0 .14rem;

          opacity: 0;
          transition: all 4s;
          &.ny-bar-prize-txt-active {
            opacity: 1;
          }
          span {
            padding: .03rem 0;
          }
        }
        .ny-bar-prize-num {
          font-size: 0.18rem;
          color: lighten(#fbe709, 30%);
          width: 100%;
          margin-top: .38rem;
          margin-left: .08rem;
        }
        .ny-bar-prize-finished {
          @extend .ny-back-reset;
          background-image: url(../assets/images/prize-item-ok.png);
          width: .55rem;
          height: .55rem;
          position: absolute;
          bottom: .5rem;
          right: -.16rem;
        }
      }
      .ny-bar-extra-fu {
        @extend .ny-back-reset;
        background-image: url(../assets/images/bee-1.png);
        width: 2.49rem*.6;
        height: 2.44rem*.6;
        position: absolute;
        top: -1.5rem;
        right: .5rem;
        .ny-bar-extra-fu-fire {
          position: absolute;
          top: -.1rem;
          left: .2rem;
          /*background-color: rgba(33, 33, 33, .7);*/
          /*width: 1rem;*/
          /*height: 1rem;*/
          @keyframes scale {
            0% {
              transform: scale(1);
            }
            50%,
            75% {
              transform: scale(4);
            }
            78%, 100% {
              opacity: 0;
            }
          }
          > span {
            background-color: #fedc09;
            border-radius: 50%;
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            &:before {
              content: '';
              border-radius: 50%;
              background-color: #eed968;
              opacity: 0.7;
              animation: scale 2s infinite cubic-bezier(0, 0, 0.49, 1.02);
              transition: 0.5s all ease;
              transform: scale(1);
            }
          }
          @mixin calculate-size($s) {
            width: #{$s}rem;
            height: #{$s}rem;
          }
          .fu-fire-1, .fu-fire-1:before {
            @include calculate-size(.04);
            top: .5rem;
            left: -.01rem;
            animation-delay: 200ms;
          }
          .fu-fire-2, .fu-fire-2:before {
            @include calculate-size(.035);
            top: .46rem;
            left: -.025rem;
            animation-delay: 800ms;
          }
          .fu-fire-3, .fu-fire-3:before {
            @include calculate-size(.035);
            top: .38rem;
            left: .05rem;
            animation-delay: 1400ms;
          }
        }
      }
    }

    .ny-hpb {
      padding: 100px 0 0 0;
      height: 100%;
      background-color: rgba(0, 0, 0, .45);
      > div {
        width: 600px;
        margin: 0 auto;
      }
    }
    .ny-tdb {
      padding: 200px 0 0 0;
      height: 100%;
      background-color: rgba(0, 0, 0, .45);
      transition: all .5s;
      transform: translateX(-1px);
      > div {
        margin: 0 auto;
      }
    }

    .tdb-enter-active, .tdb-leave-active {
      transition: all 1s ease;
      .drawbox {
        transition: all .5s ease;
        transform: translateY(0px);
      }
      .tdb-footer {
        transition: all .5s ease;
        transform: translateY(0px);
      }
    }
    .tdb-enter, .tdb-leave-active {
      opacity: 0;
      .drawbox {
        transform: translateY(-30px);
      }
      .tdb-footer {
        transform: translateY(30px);
      }
    }

    .ny-tdb-transform {
      transition: all .5s;
      transform: translateX(-260px)
    }
    .ny-list {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.75);
      z-index: 999;
    }
    .nyl-enter-active, .nyl-leave-active {
      transition: all .5s ease;
      .prize-person-list {
        transition: all .5s ease;
      }
    }
    .nyl-enter, .nyl-leave-active {
      opacity: 0.1;
      .prize-person-list {
        transform: scale(.3);
      }
    }
  }
</style>
