<template>
  <div class="confirm-box" v-if="winShow">
    <div class="confirm-container">
      <div class="confirm-close" @click="hide()"></div>
      <div class="confirm-title">{{title}}</div>
      <div class="confirm-content">{{content}}</div>
      <div class="confirm-footer">
        <button @click="onCancel">取消</button>
        <button @click="onSubmit">确认</button>
      </div>
    </div>
  </div>
</template>

<script>

// 但愿这个组件永远不会被调起
export default {
  name: 'tipBox',
  data () {
    return {
      winShow: false,
      title: '',
      content: ''
    }
  },
  methods: {
    show (title, content) {
      this.winShow = true
      this.title = title
      this.content = content
    },
    hide () {
      this.winShow = false
      this.title = '新年快乐！'
      this.content = '万事如意'
    },
    onCancel () {
      this.$emit('onCancel')
    },
    onSubmit () {
      this.$emit('onSubmit')
    }
  },
  mounted () {
  }
}
</script>

