<template>
  <div class="prize-person-list">
    <div class="ppl-decoration-1"></div>
    <div class="ppl-decoration-2"></div>
    <div class="ppl-title">{{prize.item.join('') || '获奖名单'}}</div>
    <div class="ppl-close" @click="close()"></div>
    <ul class="ppl-list" :class="'ppl-list-' + list.length">
      <li v-for="(item, index) in list" :key="index">
        <div>
          <span class="number">{{item.lno}}</span>
          <span class="name">{{item.name}}</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'prizePersonList',
  props: {
    list: Array,
    prize: {
      type: Object,
      'default': {}
    }
  },
  data () {
    return {
      c_list: []
    }
  },
  methods: {
    push () {
      this.c_list = []
      const tempAry = this.list.slice(0)
      let timer = window.setInterval(() => {
        this.c_list.push(tempAry.shift())
        if (tempAry.length <= 0) {
          window.clearInterval(timer)
        }
      }, 500)
    },
    close () {
      this.$emit('onClose')
    }
  },
  components: {},
  mounted () {
  },
  watch: {
    list (newV, oldV) {
      console.info(newV, oldV)
      this.push()
    }
  }
}
</script>

